<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="querySearch || '搜索商品'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div class="sort-item">综合</div>
      <div class="sort-item">销量</div>
      <div class="sort-item">价格 </div>
    </div>

    <div class="goods-list">
      <GoodsItem v-for="item in proList" :key="item.goods_id" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import { getProList } from '@/api/product'
import GoodsItem from '@/components/GoodsItem.vue'
export default {
  name: 'SearchIndex',
  data () {
    return {
      page: 1,
      proList: []
    }
  },
  components: {
    GoodsItem
  },
  async created () {
    const { data: { list } } = await getProList({
      categoryId: this.$route.query.categoryId,
      goodsName: this.querySearch,
      page: this.page
    }) // 此处将list从所有获取的响应中结构出来，再赋给ptoList
    this.proList = list.data
    console.log(this.$route.query.categoryId)
    console.log(this.proList)
  },
  // computed用于监听相关依赖发生变化时，返回新的值
  computed: {
    // 因为前面search/index页面搜索时，url后面添加了/searchlist?search=${key}
    // 此处可以利用后面路径的search=${key}获取搜索商品的key值，明确搜索商品
    querySearch () {
      return this.$route.query.search
      // this.$route 代表获取当前页面路径
      // this.$route.query 代表获取？后面的所有当前路由的查询参数（query parameters），它是一个对象，包含了 URL 中 ? 后面的所有键值对。
      // this.$route.query.search进一步明确了要查询的键值对对象名称为search，获取到的是search对应的值
      // 这行代码的目的是为了赋值给接口的查询参数
    }
    // querycategoryId () {
    //   return this.$route.query.categoryId
    // }
  }
}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
